import * as React from "react"
import { graphql } from "gatsby"
import SEO from "../../components/seo";
import { objFit, objFitCodeString, objPos, objPosCodeString } from "../../data/object";
import { CodeSample, SectionWrapper } from "../../components/ExampleComponents";

const Object = ( { data } ) => {
    const images = data.allImageSharp.nodes;
    return (
        <>
            <SEO title={ "Object" }/>
            <h1>Object</h1>
            <p>The class constructor looks like this:</p>
            <CodeSample label={ "For Object Fit" } codeString={ `obj__fit--{value}` }/>
            <CodeSample label={ "For Object Position" } codeString={ `obj__pos--{value}` }/>
            <SectionWrapper id={ "object-fit" } codeString={ objFitCodeString.join( '' ) }>
                <div className={ "grid " } style={ {
                    gridTemplateColumns: "repeat(auto-fit, minmax(200px, 1fr))",
                    gap                : 20
                } }>
                    { objFit.map( fit => (
                        <div key={ fit }>
                            <img className={ `brd obj__fit--${ fit }` } width={ 200 } height={ 200 } src={ images[1].original.src } alt="sample"/>
                            <p>.obj__fit--{ fit }</p>
                        </div>
                    ) ) }
                </div>
            </SectionWrapper>
            <SectionWrapper id={ "object-position" } codeString={ objPosCodeString.join( '' ) }>
                <div className={ "grid " } style={ {
                    gridTemplateColumns: "repeat(auto-fit, minmax(200px, 1fr))",
                    gap                : 20
                } }>
                    { objPos.map( pos => (
                        <div key={ pos }>
                            <img className={ `brd obj__fit--none obj__pos--${ pos }` } width={ 200 } height={ 200 } src={ images[0].original.src } alt="sample"/>
                            <p>.obj__pos--{ pos }</p>
                        </div>
                    ) ) }
                </div>
            </SectionWrapper>
        </>
    )
}

export default Object

export const query = graphql`
  {
    allImageSharp(filter: {id: {nin: ["d47e9a54-5efa-5edc-8fbc-7f089aa4e9d4", "d8e2f2ba-3866-5886-a5bf-9b1262a7562d"]}}) {
      nodes {
        original {
          src
        }
      }
    }
  }
`
